<template>
    <div>
        <form @submit.prevent="save">

        <div class="modal-card" >
                    <header class="modal-card-head">
                        <p class="modal-card-title">Detalii bon carburant</p>
                    </header>
                    <section class="modal-card-body">



<div class="field is-horizontal">
  <div class="field-label is-normal">
    <label class="label">Data </label>
  </div>
  <div class="field-body">
    <div class="field">
      <p class="control">
        <b-datepicker placeholder="Data bon" v-model="fuel.date"  icon="calendar-today">
        </b-datepicker>        
      </p>     
    </div>    
  </div>
</div>


<div class="field is-horizontal">
  <div class="field-label is-normal">
    <label class="label">Autovehicul</label>
  </div>
  <div class="field-body">
    <div class="field is-narrow">
      <div class="control">
        <div class="select is-fullwidth">
          <select v-model="fuel.vehicleId">
            <option value="">Alege autovehicul</option>
            <option v-for="v in vehicles" :key="v.id" :value="v.id" >{{v.name}}</option>
          </select>         
        </div>
         <p class="help is-danger" v-if="$v.fuel.vehicleId.$dirty && !$v.fuel.vehicleId.required">
          Alege autovehiculul     
      </p>
      </div>
    </div>
  </div>
</div>



<div class="field is-horizontal">
  <div class="field-label is-normal">
    <label class="label">Kms la bord</label>
  </div>
  <div class="field-body">
    <div class="field">
      <p class="control">
        <input class="input" type="number" v-model="fuel.kms" placeholder="">      
      </p>
      <p class="help is-danger" v-if="$v.fuel.kms.$dirty && !$v.fuel.kms.required">
          Specifica numarul de km la bord.     
      </p>
    </div>    
  </div>
</div>


<div class="field is-horizontal">
  <div class="field-label is-normal">
    <label class="label">Total Litri</label>
  </div>
  <div class="field-body">
    <div class="field">
      <p class="control">
        <input class="input" type="number" v-model="fuel.litres" >       
      </p>
      <p class="help is-danger" v-if="$v.fuel.litres.$dirty && !$v.fuel.litres.required">
          Specifica litrii alimentati.     
      </p>
    </div>    
  </div>
</div>





<div class="field is-horizontal">
  <div class="field-label is-normal">
    <label class="label">Total bon</label>
  </div>
  <div class="field-body">
    <div class="field">
      <p class="control is-expanded">
        <input class="input" type="number" v-model="fuel.price" >       
      </p>
      <p class="help is-danger" v-if="$v.fuel.price.$dirty && !$v.fuel.price.required">
          Specifica totalul de pe bon.     
      </p>
    </div>    
  </div>
</div>


<div class="field is-horizontal">
  <div class="field-label is-normal">
    <label class="label">Alimentare partiala</label>
  </div>
  <div class="field-body">
    <div class="field">
      <p class="control">
        <b-switch v-model="fuel.isPartial"></b-switch> 
      </p>
    </div>    
  </div>
</div>


<div class="field is-horizontal">
  <div class="field-label is-normal">
    <label class="label">Observatii</label>
  </div>
  <div class="field-body">
    <div class="field">
      <p class="control">
        <textarea class="textarea" v-model="fuel.details" placeholder="informatii suplimentare"></textarea>   
      </p>
    </div>    
  </div>
</div>

</section>
    <footer class="modal-card-foot">
        <button class="button" type="button" @click="$parent.close()">Renunta</button>
        <button class="button is-primary">Salveaza</button>
    </footer>
</div>

</form>
    </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

import { required } from 'vuelidate/lib/validators';  

export default {
    name:"FuelForm",
    computed:{
          ...mapGetters(['vehicles'])
        },
    data: function() {
    return {
      fuel: {
        vehicleId: '',
        date: new Date(),
        kms: null,
        litres: null,
        price: null,
        isPartial: false,
        details: ""
      }
    };
  },
  validations:{
    fuel:{
      vehicleId:{required},
      kms:{required},
      litres:{required},
      price:{required},
      }
  },
   methods: {
    ...mapActions(["saveFuel","loadFuels"]),
    save() {
         //todo: validate 
          this.$v.$touch()
        if (!this.$v.$invalid) {
            // this.axios.post("/api/fuels", this.fuel)
            //       .then((resp) => this.$parent.close())
            //       .catch((err) => console.error('error save fuel: ' + err));
          this.saveFuel(this.fuel)
            .then(resp =>{
                this.loadFuels();
                this.$parent.close();
            })
            .catch(err => console.error("error: " + err));
        }
        
    }
  },
}
</script>

