<template>
     <div>
         <div class="tile">
            <div class="tile is-parent is-vertical">
                <article class="tile is-child notification is-primary">
                <p class="title">{{vehiclesTitle}}</p>
                <p class="subtitle"><button  class="button is-small" @click="addVehicle">+ adauga</button></p>
                </article>
            </div>
        </div>
        <table class="table is-striped is-fullwidth is-hoverable is-responsive ">
            <thead>
                <tr>
                <th>Id</th>
                <th>Nr. auto</th>
                <th>Model</th>
                <th>Combustibil</th>
                <th></th>
                </tr>
            </thead>
            <tbody>
                <tr v-show="vehicles.length > 0" v-for="v in vehicles" :key="v.id">
                <th>{{v.id}}</th>
                <td>{{v.name}}</td>
                <td>{{v.model}}</td>
                <td>{{v.fuelTypeName}}</td>
                <td>
                    <button>edit</button>
                    <button @click="del(v)">sterge</button>
                </td>
                </tr>
                <tr>
                    <td v-show="vehicles.length == 0" colspan="5" class="has-text-centered is-size-3">
                        <h1>Nu am gasit nici un autovehicul.</h1>
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
import VehicleForm from "@/components/VehicleForm"
import {mapGetters, mapActions} from 'vuex'

export default {
  name: 'dashboard',
  computed:{
      ...mapGetters(['vehicles']),
      vehiclesTitle(){
         return this.vehicles && this.vehicles.length == 1 ?
          "1 autovehicul": `${this.vehicles.length} autovehicule`
      }
  },

        methods:{
            ...mapActions(['loadVehicles', 'delVehicle']),
            addVehicle: function() {
                this.$modal.open({
                    parent: this,
                    component: VehicleForm,
                    hasModalCard: true
                });
            },
            del(v){
                 this.$dialog.confirm({
                    message: `Sigur stergem: ${v.name} ?`,
                    onConfirm: () =>{
                         this.delVehicle(v.id)
                         .then(()=>{
                             this.loadVehicles();
                             this.$toast.open('Autovehicul sters!')
                         })
                         .catch(err => console.log(err));
                         
                         }
                })
               
            }
        },
        created(){this.loadVehicles();}
}
</script>
