<template>
     <div>
              <div class="tile">
            <div class="tile is-parent is-vertical">
                <article class="tile is-child notification is-primary">
                <p class="title">{{fuelsTitle}}</p>
                <p class="subtitle"><button class="button is-small" @click="addFuel">+ adauga</button></p>
                </article>
            </div>
        </div>
      <table class="table is-striped is-fullwidth is-hoverable is-responsive">
            <thead>
                <tr>
                <th>Id</th>
                <th>Nr. auto</th>
                <th>Data</th>
                <th>Km</th>
                <th>Litri</th>
                <th>Pret/Litru</th>
                <th>Total</th>
                <th></th>
                </tr>
            </thead>
            <tbody>
                <tr v-show="fuels.length > 0" v-for="f in fuels" :key="f.id">
                <th>{{f.id}}</th>
                <td>{{f.vehicleName}}</td>
                <td>{{f.date}}</td>
                <td>{{f.kms}}</td>
                <td>{{f.litres}}</td>
                <td>{{f.litrePrice}}</td>
                <td>{{f.price}}</td>
                <td>
                    <button>edit</button>
                    <button @click="del(f)">sterge</button>
                </td>
                </tr>
                <tr>
                    <td v-show="fuels.length == 0" colspan="5" class="has-text-centered is-size-3">
                        <h1>Nu am gasit nici un bon de consum.</h1>
                    </td>
                </tr>
            </tbody>
        </table>
      
    </div>
</template>
<script>
import FuelForm from "@/components/FuelForm"
import {mapGetters, mapActions} from 'vuex'


export default {
  name: 'dashboard',
  computed:{
      ...mapGetters(['fuels']),
       fuelsTitle(){
         return this.fuels && this.fuels.length == 1 ?
          "1 bon carburant": `${this.fuels.length} bonuri` + ` carburant`
      }
      },
       
        methods:{
            ...mapActions(['loadFuels','delFuel']),
            addFuel: function() {
                this.$modal.open({
                    parent: this,
                    component: FuelForm,
                    hasModalCard: true
                });
            },
            del(f){
                 this.$dialog.confirm({
                    message: `Sigur stergem bonul de carburant: ${f.id} din ${f.date} in valoare de ${f.price} lei ?`,
                    onConfirm: () =>{
                         this.delFuel(f.id)
                         .then(()=>{
                             this.loadFuels();
                             this.$toast.open('Bon carburant sters!')
                         })
                         .catch(err => console.log(err));
                         
                         }
                })               
            }
        },
        created() { this.loadFuels();}
}
</script>
