<script>

import VehicleForm from "@/components/VehicleForm"
import FuelForm from "@/components/FuelForm"

import {mapGetters, mapActions} from 'vuex'

export default {
  name: 'dashboard',
   data () {
            return {
                data1: [
                {
                    chartType: 'line',
                    values: [5, 6, 3.7, 15, 8, 5, 7, 3]
                }],
                 data2: [
                {
                    chartType: 'line',
                    values: [8, 6, 7, 8, 8, 5, 7, 7]
                }]
            }
        },
        computed:{
          ...mapGetters(['vehicles','fuels']),
          fuelsTitle(){
            return this.fuels && this.fuels.length == 1 ?
              "1 bon carburant": `${this.fuels.length} bonuri` + ` carburant`
          },
          vehiclesTitle(){
            return this.vehicles && this.vehicles.length == 1 ?
              "1 autovehicul": `${this.vehicles.length} autovehicule`
          }
        },
        methods:{
          ...mapActions(['loadVehicles']),
           addVehicle: function() {
                this.$modal.open({
                    parent: this,
                    component: VehicleForm,
                    hasModalCard: true
                });
            },
            addFuel: function() {
                this.$modal.open({
                    parent: this,
                    component: FuelForm,
                    hasModalCard: true
                });
            }
        },
        created(){this.loadVehicles()}
}
